import React from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { formatCurrency, formatPercentage, getChangeColorClass } from '@/lib/utils';

// Enhanced stock price data interface with 52-week data
interface StockPriceData {
  symbol: string;
  name: string;
  price: number;
  change: number;
  changePercent: number;
  volume: number;
  marketCap?: number;
  high?: number;
  low?: number;
  open?: number;
  // 52-week data
  week52High?: number;
  week52Low?: number;
  week52HighDate?: string;
  week52LowDate?: string;
}

// Stock price card props
interface StockPriceCardProps {
  data: StockPriceData;
  variant?: 'default' | 'compact' | 'detailed';
  className?: string;
  onClick?: () => void;
}

// Enhanced arrow icon component for price changes with animations
const PriceArrow = ({ change }: { change: number }) => {
  if (change === 0) {
    return (
      <div className="flex items-center justify-center w-6 h-6 rounded-full bg-gray-100 dark:bg-gray-800">
        <svg className="w-4 h-4 text-gray-500" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 12h14" />
        </svg>
      </div>
    );
  }
  
  return change > 0 ? (
    <div className="flex items-center justify-center w-6 h-6 rounded-full bg-green-100 dark:bg-green-900/30 animate-bounce-slow">
      <svg className="w-4 h-4 text-green-600 dark:text-green-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 10l7-7m0 0l7 7m-7-7v18" />
      </svg>
    </div>
  ) : (
    <div className="flex items-center justify-center w-6 h-6 rounded-full bg-red-100 dark:bg-red-900/30 animate-bounce-slow">
      <svg className="w-4 h-4 text-red-600 dark:text-red-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
      </svg>
    </div>
  );
};

// Enhanced 52-week range indicator component with animations
const Week52Range = ({ currentPrice, week52High, week52Low }: { 
  currentPrice: number; 
  week52High?: number; 
  week52Low?: number; 
}) => {
  if (!week52High || !week52Low) return null;

  const range = week52High - week52Low;
  const position = ((currentPrice - week52Low) / range) * 100;
  
  return (
    <div className="space-y-3 animate-fade-in">
      <div className="flex justify-between text-xs text-gray-600 dark:text-gray-400">
        <span className="font-medium">52W Low: {formatCurrency(week52Low)}</span>
        <span className="font-medium">52W High: {formatCurrency(week52High)}</span>
      </div>
      <div className="relative h-3 bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden">
        <div 
          className="absolute h-3 bg-gradient-to-r from-red-500 via-yellow-500 to-green-500 rounded-full transition-all duration-500 ease-out"
          style={{ width: '100%' }}
        />
        <div 
          className="absolute top-0 h-3 w-2 bg-white dark:bg-gray-900 rounded-full shadow-lg transition-all duration-500 ease-out animate-float"
          style={{ left: `${Math.max(0, Math.min(100, position))}%` }}
        />
      </div>
      <div className="text-xs text-gray-500 dark:text-gray-400 text-center font-medium">
        Current: {formatCurrency(currentPrice)}
      </div>
    </div>
  );
};

// Enhanced loading shimmer component
const LoadingShimmer = ({ className = "" }: { className?: string }) => (
  <div className={`loading-shimmer rounded ${className}`} />
);

// Main stock price card component with enhanced animations
export const StockPriceCard: React.FC<StockPriceCardProps> = ({
  data,
  variant = 'default',
  className,
  onClick,
}) => {
  const isPositive = data.change >= 0;
  const changeColorClass = getChangeColorClass(data.change);

  // Compact variant for smaller displays with enhanced animations
  if (variant === 'compact') {
    return (
      <Card 
        className={`card-enhanced hover-lift cursor-pointer ${className}`}
        onClick={onClick}
      >
        <CardContent className="p-4">
          <div className="flex items-center justify-between animate-fade-in">
            <div className="flex-1 min-w-0">
              <div className="flex items-center space-x-3">
                <h3 className="font-semibold text-gray-900 dark:text-white truncate">
                  {data.symbol}
                </h3>
                <PriceArrow change={data.change} />
              </div>
              <p className="text-sm text-gray-600 dark:text-gray-400 truncate mt-1">
                {data.name}
              </p>
            </div>
            <div className="text-right">
              <div className="font-semibold text-gray-900 dark:text-white text-lg">
                {formatCurrency(data.price)}
              </div>
              <div className={`text-sm font-medium ${changeColorClass} animate-pulse`}>
                {isPositive ? '+' : ''}{formatCurrency(data.change)} ({formatPercentage(data.changePercent)})
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }

  // Enhanced detailed variant with 52-week data and animations
  if (variant === 'detailed') {
    return (
      <Card 
        className={`card-enhanced hover-lift cursor-pointer ${className}`}
        onClick={onClick}
      >
        <CardHeader className="pb-3">
          <div className="flex items-center justify-between animate-slide-in">
            <div>
              <CardTitle className="text-lg font-bold text-gray-900 dark:text-white flex items-center space-x-2">
                <span>{data.symbol}</span>
                <div className="w-2 h-2 bg-blue-500 rounded-full animate-pulse"></div>
              </CardTitle>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                {data.name}
              </p>
            </div>
            <div className="flex items-center space-x-2">
              <PriceArrow change={data.change} />
              <span className={`text-sm font-medium px-2 py-1 rounded-full ${changeColorClass} bg-opacity-10`}>
                {formatPercentage(data.changePercent)}
              </span>
            </div>
          </div>
        </CardHeader>
        
        <CardContent className="space-y-6">
          {/* Main price display with enhanced styling */}
          <div className="text-center animate-scale-in">
            <div className="text-3xl font-bold text-gray-900 dark:text-white mb-1">
              {formatCurrency(data.price)}
            </div>
            <div className={`text-lg font-medium ${changeColorClass} px-3 py-1 rounded-full inline-block bg-opacity-10`}>
              {isPositive ? '+' : ''}{formatCurrency(data.change)}
            </div>
          </div>

          {/* 52-week range indicator */}
          {data.week52High && data.week52Low && (
            <div className="border-t border-gray-200 dark:border-gray-700 pt-4">
              <Week52Range 
                currentPrice={data.price}
                week52High={data.week52High}
                week52Low={data.week52Low}
              />
            </div>
          )}

          {/* Enhanced metrics grid */}
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div className="space-y-1">
              <p className="text-gray-600 dark:text-gray-400 font-medium">Volume</p>
              <p className="font-semibold text-gray-900 dark:text-white">
                {data.volume.toLocaleString()}
              </p>
            </div>
            {data.marketCap && (
              <div className="space-y-1">
                <p className="text-gray-600 dark:text-gray-400 font-medium">Market Cap</p>
                <p className="font-semibold text-gray-900 dark:text-white">
                  {formatCurrency(data.marketCap, 'INR', 'en-IN')}
                </p>
              </div>
            )}
            {data.high && data.low && (
              <>
                <div className="space-y-1">
                  <p className="text-gray-600 dark:text-gray-400 font-medium">Day High</p>
                  <p className="font-semibold text-green-600 dark:text-green-400">
                    {formatCurrency(data.high)}
                  </p>
                </div>
                <div className="space-y-1">
                  <p className="text-gray-600 dark:text-gray-400 font-medium">Day Low</p>
                  <p className="font-semibold text-red-600 dark:text-red-400">
                    {formatCurrency(data.low)}
                  </p>
                </div>
              </>
            )}
            {data.week52High && data.week52Low && (
              <>
                <div className="space-y-1">
                  <p className="text-gray-600 dark:text-gray-400 font-medium">52W High</p>
                  <p className="font-semibold text-green-600 dark:text-green-400">
                    {formatCurrency(data.week52High)}
                  </p>
                </div>
                <div className="space-y-1">
                  <p className="text-gray-600 dark:text-gray-400 font-medium">52W Low</p>
                  <p className="font-semibold text-red-600 dark:text-red-400">
                    {formatCurrency(data.week52Low)}
                  </p>
                </div>
              </>
            )}
          </div>

          {/* 52-week dates if available */}
          {data.week52HighDate && data.week52LowDate && (
            <div className="text-xs text-gray-500 dark:text-gray-400 space-y-1 bg-gray-50 dark:bg-gray-800 p-3 rounded-lg">
              <div className="flex justify-between">
                <span>52W High:</span>
                <span>{new Date(data.week52HighDate).toLocaleDateString()}</span>
              </div>
              <div className="flex justify-between">
                <span>52W Low:</span>
                <span>{new Date(data.week52LowDate).toLocaleDateString()}</span>
              </div>
            </div>
          )}
        </CardContent>
      </Card>
    );
  }

  // Enhanced default variant with 52-week info and animations
  return (
    <Card 
      className={`card-enhanced hover-lift cursor-pointer ${className}`}
      onClick={onClick}
    >
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between animate-fade-in">
          <div>
            <CardTitle className="text-lg font-semibold text-gray-900 dark:text-white flex items-center space-x-2">
              <span>{data.symbol}</span>
              <div className="w-1.5 h-1.5 bg-blue-500 rounded-full animate-pulse"></div>
            </CardTitle>
            <p className="text-sm text-gray-600 dark:text-gray-400 truncate">
              {data.name}
            </p>
          </div>
          <PriceArrow change={data.change} />
        </div>
      </CardHeader>
      
      <CardContent>
        <div className="space-y-3">
          <div className="text-2xl font-bold text-gray-900 dark:text-white animate-scale-in">
            {formatCurrency(data.price)}
          </div>
          <div className="flex items-center justify-between">
            <div className={`text-sm font-medium ${changeColorClass} px-2 py-1 rounded-full bg-opacity-10`}>
              {isPositive ? '+' : ''}{formatCurrency(data.change)}
            </div>
            <div className={`text-sm font-medium ${changeColorClass} px-2 py-1 rounded-full bg-opacity-10`}>
              {formatPercentage(data.changePercent)}
            </div>
          </div>
          <div className="text-xs text-gray-500 dark:text-gray-400 flex items-center space-x-1">
            <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
            </svg>
            <span>Vol: {data.volume.toLocaleString()}</span>
          </div>
          
          {/* 52-week range in default variant */}
          {data.week52High && data.week52Low && (
            <div className="pt-3 border-t border-gray-100 dark:border-gray-700">
              <div className="flex justify-between text-xs text-gray-500 dark:text-gray-400">
                <span>52W: {formatCurrency(data.week52Low)}</span>
                <span>{formatCurrency(data.week52High)}</span>
              </div>
              <div className="mt-1 h-1 bg-gray-200 dark:bg-gray-700 rounded-full overflow-hidden">
                <div 
                  className="h-1 bg-gradient-to-r from-red-500 via-yellow-500 to-green-500 rounded-full transition-all duration-300"
                  style={{ width: `${Math.max(0, Math.min(100, ((data.price - data.week52Low) / (data.week52High - data.week52Low)) * 100))}%` }}
                />
              </div>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
};

// Enhanced loading skeleton for stock price card with shimmer effect
export const StockPriceCardSkeleton = ({ variant = 'default' }: { variant?: 'default' | 'compact' | 'detailed' }) => {
  if (variant === 'compact') {
    return (
      <Card className="animate-pulse">
        <CardContent className="p-4">
          <div className="flex items-center justify-between">
            <div className="flex-1 space-y-2">
              <LoadingShimmer className="h-4 w-16" />
              <LoadingShimmer className="h-3 w-24" />
            </div>
            <div className="space-y-2">
              <LoadingShimmer className="h-4 w-20" />
              <LoadingShimmer className="h-3 w-16" />
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }

  if (variant === 'detailed') {
    return (
      <Card className="animate-pulse">
        <CardHeader className="pb-3">
          <div className="flex items-center justify-between">
            <div className="space-y-2">
              <LoadingShimmer className="h-5 w-16" />
              <LoadingShimmer className="h-3 w-24" />
            </div>
            <LoadingShimmer className="h-4 w-12" />
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="text-center space-y-2">
            <LoadingShimmer className="h-8 w-32 mx-auto" />
            <LoadingShimmer className="h-5 w-20 mx-auto" />
          </div>
          <LoadingShimmer className="h-12 w-full" />
          <div className="grid grid-cols-2 gap-4">
            {[1, 2, 3, 4, 5, 6].map((i) => (
              <div key={i} className="space-y-1">
                <LoadingShimmer className="h-3 w-12" />
                <LoadingShimmer className="h-4 w-16" />
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="animate-pulse">
      <CardHeader className="pb-3">
        <div className="flex items-center justify-between">
          <div className="space-y-2">
            <LoadingShimmer className="h-5 w-16" />
            <LoadingShimmer className="h-3 w-24" />
          </div>
          <LoadingShimmer className="h-4 w-4" />
        </div>
      </CardHeader>
      <CardContent className="space-y-2">
        <LoadingShimmer className="h-7 w-24" />
        <div className="flex justify-between">
          <LoadingShimmer className="h-4 w-16" />
          <LoadingShimmer className="h-4 w-12" />
        </div>
        <LoadingShimmer className="h-3 w-20" />
        <LoadingShimmer className="h-3 w-24" />
      </CardContent>
    </Card>
  );
};
